// Checkbox.tsx
import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { FormFieldProps, FormFieldScope } from './form-field.interface';

export const FormField: React.FC<FormFieldProps> = ({
  messageId,
  required,
  children,
  state = 'default',
  message,
  ...props
}) => {
  return (
    <fieldset
      className={clsx(FormFieldScope, 'inline-flex', 'flex-col', 'flex-nowrap')}
      data-testid="zep-form-fieldset"
      {...props}
    >
      {children}
      <span
        id={messageId}
        className={clsx(
          { 'text-zep-error': state === 'error' || required, 'text-zep-info': state === 'info' },
          'mt-2 flex flex-row items-center gap-2',
        )}
      >
        {(state === 'error' || required) && <FunctionalIcon name="Warning" className="text-zep-error" />}
        {state === 'info' && <FunctionalIcon name="Info" className="text-zep-info" />}
        {message}
      </span>
    </fieldset>
  );
};
